:root {
  --tan: #e5d9b6;
  --light-green: #a4be7b;
  --green: #5f8d4e;
  --dark-green: #285430;
  --imghover: rgba(136, 136, 136, 0.3);
}

* {
  margin: 0;
  color: black;
  padding: 0;
}

header {
  display: flex;
  justify-content: space-between;
  height: 50px;
  background-color: var(--green);
}

ul {
  list-style: none;
}

.main {
  width: 100%;
  background-color: var(--light-green);
}

body {
  background-color: var(--tan);
}

footer {
  width: 100%;
  height: 50px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.nav-link:hover {
  background-color: var(--dark-green);
}

.active {
  background-color: var(--dark-green);
}

.portfolioImage {
  size: 300px;
}

li {
  float: left;
}

.navbar li a {
  display: block;
  color: black;
  text-align: center;
  text-decoration: none;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.form {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.form input {
  margin-bottom: 5px;
}

.smallimg {
  height: 40px;
  margin: 0 5px;
  border-radius: 5px;
}

.smallimg:hover {
  background-color: var(--imghover);
}

.portfoliocard {
  margin-top: 10px;
  width: 300px;
  height: auto;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: none;
}

.portfoliocard a {
  visibility: hidden;
}

.portfoliocard:hover a {
  visibility: visible;
}

.cardlink div {
  height: 50%;
  width: 100%;
  text-decoration: none;
  text-align: center;
}

.cardlink div:hover {
  background-color: var(--imghover);
}

.cardlink div p {
  margin: auto;
  padding-top: 20%;
  padding-bottom: 20%;
}

#aboutmediv {
  padding: 10px;
  display: flex;
  flex-direction: row;
}

#aboutmediv img {
  height: 200px;
  width: 200px;
}

#aboutmediv p {
  padding-left: 10px;
}

#portfoliodiv {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  flex: 0 0 2em;
  margin: 30px 0;
}

#resumediv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#resumediv a button {
  margin: 10px;
  height: 30px;
  width: 80px;
}

#resumediv img {
  margin-bottom: 10px;
}

@media (max-width: 480px) {
  header {
    flex-direction: column;
    text-align: center;
    justify-content: center;
    height: auto;
  }

  .navbar li {
    float: none;
  }

  .navbar li a {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  #aboutmediv {
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }

  #aboutmediv img {
    margin-left: auto;
    margin-right: auto;
  }
}
